:host {
  font-size: 10px;
}

.dav {

  &-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 16px;
    //padding: 0 8px;

    &__textfield {
      width: 100%;
    }

    &__icon {
      margin-left: 8px;
      background-color: $red_primary;
      width: 40px;
      min-width: 40px;
      height: 40px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;

      &__img {
        width: 24px;
        height: 24px;
      }

      &__img-send {
        width: 28px;
        height: 28px;
        margin-right: 4px;
      }

      &__inactive {
        @extend .dav-footer__icon;
        background-color: $option_hover;
        pointer-events: none;
      }
    }

  }

  &__full-width {
    width: 100%;
  }

  &__equalizer {
    width: 60px;
    height: 50px;
  }
}

.noPadding {
  padding-bottom: 0px;
}

.padding-x {
  margin-left: 8px;
  margin-right: 8px;
}

%textFieldGeneral {
  width: 100%;
  height: 40px;
  padding: 8px;
  border: 1px solid #D1D5E0;
  border-radius: 8px;
  background-color: #ffffff;
  color: #404040;
  font-size: $font_small;
  font-weight: 400;
  box-sizing: border-box;
  transition: all .2s ease;

  &:-ms-reveal,
  &:-ms-clear {
    display: none;
  }
}

.dav-textField {
  @extend %textFieldGeneral;
  position: relative;
  display: flex;
  gap: 8px;
  align-items: center;

  input {
    // width: 100%;
    border: none;
    background-color: transparent;

    flex: 1;

    &:focus {
      outline: none;
    }

    &::placeholder {
      color: #9D9D9D;
    }

    &:disabled {
      cursor: not-allowed;
    }

  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 24px;
    width: 24px;
    cursor: pointer;

    img {
      display: inline-block;
      width: auto;
    }

  }

  &__disabled {
    @extend .dav-textField;
    background-color: #F7F7F7;
    color: #9D9D9D;
    cursor: not-allowed;
    border: 1px solid #EDEDED;
  }

  &__focused {
    border: 1px solid #AAB1BC;
  }

  &__error {
    border: 1px solid #B70412;
  }

}